(function () {
  'use strict';

  angular
    .module('trcReln', [
      'ngResource'
    ]);

})();

// NOTE: this entire API model *WILL* eventually change

/**
 * @typedef Relationship
 * @type {object}
 * @property {string}     id                  - A unique identifier for this relationship
 * @property {string}     typeId              - The ID of the RelationshipType of this relationship
 * @property {string}     description         - A detailed description of this relationship
 * @property {string}     descriptionMimeType - MIME type string of the description
 * @property {Provenance} provenance          - Metadata about the relationship record
 * @property {Anchor[]}   relatedEntities     - A set of "source" anchors
 * @property {Anchor[]}   targetEntities      - A set of "target" anchors
 */

/**
 * @typedef RelationshipType
 * @type {object}
 * @property {string}  identifier   - A unique identifier for this type
 * @property {string}  title        - A label to display for this type
 * @property {string}  reverseTitle - If this type is directed, the label to display for reverse relationships
 * @property {string}  description  - A more detailed description of this type
 * @property {boolean} isDirected   - Whether this type is directed (true) or undirected (false)
 */

/**
 * @typedef Provenance
 * @type {object}
 * @property {string[]} creatorUris  - URIs to people/authors of this relationship
 * @property {string}   dateCreated  - an ISO 8601 date representation of when the record was created
 * @property {string}   dateModified - an ISO 8601 date representation of when the record was last modified
 */

/**
 * @typedef Anchor
 * @type {object}
 * @property {string[]} entryUris[] - URIs to work/edition/volume
 */

(function () {
  'use strict';

  angular
    .module('trcReln')
    .provider('relationshipsRepo', relationshipsRepoProvider);

  function relationshipsRepoProvider() {
    var provider = {};
    provider.url = '/api/relationships';
    provider.$get = relationshipsRepoFactory;
    return provider;

    /** @ngInject */
    function relationshipsRepoFactory($q, $resource) {
      var typeResource = $resource(provider.url + '/types/:id', { id: '@identifier' });
      var relnResource = $resource(provider.url + '/:id', { id: '@id' }, {
        update: { method: 'PUT' }
      });

      var typesCache = null;

      var repo = {};
      repo.getTypes = getTypes;
      repo.getType = getType;
      repo.createRelationship = createRelationship;
      repo.createProvenance = createProvenance;
      repo.createAnchor = createAnchor;
      repo.get = findById;
      repo.search = search;
      repo.save = saveRelationship;
      repo.delete = deleteRelationship;
      repo.normalizeRelationships = normalizeRelationships;
      return repo;

      /**
       * Fetches relationship types from the server, caching them in the process.
       * The returned array will be populated when the request resolves.
       * The underlying promise is available on the $promise property.
       *
       * @param  {object} options
       * @param  {boolean} options.force Discard cache (if set) and refetch
       * @return {object.<string,RelationshipType>}
       */
      function getTypes(options) {
        options = options || {};

        if (!typesCache || options.force) {
          var types = typeResource.query();

          typesCache = {};

          var tcPromise = types.$promise.then(function () {
            types.forEach(function (type) {
              typesCache[type.identifier] = type;
            });
            return typesCache;
          });

          // add $promise as a non-enumerable property
          Object.defineProperty(typesCache, '$promise', {
            value: tcPromise
          });
        }

        return typesCache;
      }

      /**
       * Fetches a type by ID
       *
       * @param  {string} id
       * @return {Promise.<RelationshipType>}
       */
      function getType(id) {
        var types = getTypes();

        return types.$promise.then(function (types) {
          return types[id];
        });
      }

      /**
       * Creates a new empty relationship instance
       *
       * @return {Relationship}
       */
      function createRelationship() {
        var reln = new relnResource();
        return adaptRelationship(reln);
      }

      /**
       * Creates a new empty provenance model
       *
       * @return {Provenance}
       */
      function createProvenance() {
        var provenance = {};
        return adaptProvenance(provenance);
      }

      /**
       * Creates a new empty anchor model
       *
       * @return {Anchor}
       */
      function createAnchor() {
        var anchor = {};
        return adaptAnchor(anchor);
      }

      /**
       * Retrieves a relationship from the server by ID.
       * The returned model will be populated once the request succeeds.
       * The underlying promise can be accessed via the $promise property.
       *
       * @param  {string} id
       * @return {Relationship}
       */
      function findById(id) {
        return relnResource.get({ id: id });
      }

      /**
       * Find all relationships matching the given criteria.
       * @param  {object|string} options
       * @property {string} options.uri (default if options is a string)
       * @property {string} options.typeId
       * @property {string} options.direction "from" (out-relationships), "to" (in-relationships), or "any"
       * @property {integer} options.start
       * @property {integer} options.max
       * @return {Relationship[]}
       */
      function search(options) {
        if (!options) {
          throw new Error('no options provided')
        }

        if (angular.isString(options)) {
          // treat single string as an entity URL
          options = { uri: options };
        }

        var queryOpts = {};

        if (options.uri) {
          queryOpts.entity = options.uri;
        }

        if (options.typeId) {
          queryOpts.type = options.typeId;
        }

        if (options.direction) {
          queryOpts.direction = options.direction;
        }

        if (options.start) {
          queryOpts.off = options.start;
        }

        if (options.max) {
          queryOpts.max = options.max;
        }

        return relnResource.query(queryOpts);
      }

      /**
       * Saves a relationship back to the server.
       *
       * @param  {Relationship} reln
       * @return {Promise} resolves on successful save
       */
      function saveRelationship(reln) {
        if (!(reln instanceof relnResource)) {
          throw new Error('Relationship was not created by this repo');
        }

        return reln.id
          ? reln.$update()
          : reln.$save();
      }

      /**
       * Deletes a relationship from the server via the REST API.
       *
       * @param  {string} id
       * @return {Promise} resolves on success
       */
      function deleteRelationship(id) {
        var response = relnResource.delete({ id: id });
        return response.$promise
      }

      /**
       * Ensures aggregate fields on a relationship are set appropriately
       *
       * @param  {Relationship} reln
       * @return {Relationship}
       */
      function adaptRelationship(reln) {
        if (!reln.provenance) {
          reln.provenance = createProvenance();
        }

        if (!reln.relatedEntities) {
          reln.relatedEntities = [];
        }

        if (!reln.targetEntities) {
          reln.targetEntities = [];
        }

        return reln;
      }

      /**
       * Ensures aggregate fields on a provenance model are set appropriately
       *
       * @param  {Provenance} provenance
       * @return {Provenance}
       */
      function adaptProvenance(provenance) {
        if (!provenance.creatorUris) {
          provenance.creatorUris = [];
        }

        return provenance;
      }

      /**
       * Ensures aggregate fields on an anchor model are set appropriately
       *
       * @param  {Anchor} anchor
       * @return {Anchor}
       */
      function adaptAnchor(anchor) {
        if (!anchor.entryUris) {
          anchor.entryUris = [];
        }

        return anchor;
      }

      /**
       * Normalizes relationships into type groups that are more suitable for display in the UI
       *
       * @param  {Relationship[]} relationships
       * @return {TypeGroup[]}
       */
      function normalizeRelationships(relationships, currentUri, worksRepo) {
        var normalizedRelationships = relationships.map(function (relationship) {
          var typeId = relationship.typeId;

          var isReverse = relationship.targetEntities.some(function (anchor) {
            return anchor.entryUris.some(function (uri) {
              return uri === currentUri;
            });
          });

          var targetField = isReverse ? 'relatedEntities' : 'targetEntities';

          var entities = relationship[targetField]
          .map(function (anchor) {
            return anchor.entryUris;
          })
          .reduce(function (flattened, arr) {
            return flattened.concat(arr);
          }, [])
          .map(function (uri) {
            return resolveEntityUri(uri, worksRepo);
          });

          return {
            id: relationship.id,
            typeId: typeId,
            reverse: isReverse,
            description: relationship.description,
            entities: entities
          };
        });

        var relnsByTypeDir = {};

        normalizedRelationships.forEach(function (relationship) {
          var typeId = relationship.typeId;

          if (!relnsByTypeDir.hasOwnProperty(typeId)) {
            relnsByTypeDir[typeId] = {
              fwd: [],
              rev: []
            };
          }

          relnsByTypeDir[typeId][relationship.reverse ? 'rev' : 'fwd'].push(relationship);
        });

        var typeGroups = [];

        angular.forEach(relnsByTypeDir, function (dirs, typeId) {
          getType(typeId).then(function (type) {
            if (type.isDirected) {
              // add forward group
              if (dirs.fwd.length > 0) {
                typeGroups.push({
                  label: type.title,
                  relationships: dirs.fwd
                });
              }

              // add reverse group
              if (dirs.rev.length > 0) {
                typeGroups.push({
                  label: type.reverseTitle,
                  relationships: dirs.rev
                });
              }
            } else {
              // combine forward/reverse and add as a single group
              var group = dirs.fwd.concat(dirs.rev);
              if (group.length > 0) {
                typeGroups.push({
                  label: type.title,
                  relationships: group
                });
              }
            }
          });
        });

        return typeGroups;
      }

      function resolveEntityUri(uri, worksRepo) {
        var ENTITY_URI = /^works\/([^\/]+)(?:\/editions\/([^\/]+)(?:\/volumes\/([^\/]+))?)?$/i;
        var match = ENTITY_URI.exec(uri);

        if (!match) {
          return null;
        }

        if (match[1] && match[2] && match[3]) {
          return makeVolumeEntity(match[1], match[2], match[3], worksRepo);
        } else if (match[1] && match[2]) {
          return makeEditionEntity(match[1], match[2], worksRepo);
        } else if (match[1]) {
          return makeWorkEntity(match[1], worksRepo);
        }

        return null;
      }
    }

    function makeVolumeEntity(workId, editionId, volumeId, worksRepo) {
      var edition = worksRepo.getEdition(workId, editionId);
      var volume = worksRepo.getVolume(workId, editionId, volumeId);

      var entity = {
        type: 'volume',
        refParams: {
          workId: workId,
          editionId: editionId,
          volumeId: volumeId
        },
        entity: volume
      };

      volume.$promise.then(function () {
        var title = worksRepo.getTitle(volume.titles, ['short', 'canonical', 'bibliographic']);
        if (title) {
          entity.label = title.title + (title.subtitle ? ': ' + title.subtitle : '');
        }

        edition.$promise.then(function () {
          entity.label += ' ' + edition.editionName + ' Volume ' + volume.volumeNumber;
        }, function () {
          entity.label += ' [Unknown edition] ' + volume.volumeNumber;
        });
      });

      return entity;
    }

    function makeEditionEntity(workId, editionId, worksRepo) {
      var edition = worksRepo.getEdition(workId, editionId);

      var entity = {
        type: 'edition',
        refParams: {
          workId: workId,
          editionId: editionId
        },
        entity: edition
      };

      edition.$promise.then(function () {
        var title = worksRepo.getTitle(edition.titles, ['short', 'canonical', 'bibliographic']);
        if (title) {
          entity.label = title.title + (title.subtitle ? ': ' + title.subtitle : '') + ' ' + edition.editionName;
        }
      });

      return entity;
    }

    function makeWorkEntity(workId, worksRepo) {
      var work = worksRepo.getWork(workId);

      var entity = {
        type: 'work',
        refParams: {
          workId: workId
        },
        entity: work
      };

      work.$promise.then(function () {
        var title = worksRepo.getTitle(work.titles, ['short', 'canonical', 'bibliographic']);
        if (title) {
          entity.label = title.title + (title.subtitle ? ': ' + title.subtitle : '');
        }
      });

      return entity;
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('trcBio', [
      'ngResource'
    ]);

})();

/**
 * Data model representing a person returned by the TRC REST API
 * @typedef Person
 * @type {object}
 * @property {string}           id       - a unique identifier for this person; assigned upon creation
 * @property {EntryMeta}        meta     - metadata about the record and its history in the system
 * @property {PersonName}       name     - primary name used to identify this person
 * @property {PersonName[]}     altNames - other names by which this person is known
 * @property {?HistoricalEvent} birth    - the date and location this person was born; may be null if unknown
 * @property {?HistoricalEvent} death    - the date and location this person was born; may be null if unknown or still alive
 * @property {string}           summary  - an HTML summary of the person and his or her significance
 */

/**
 * Simplified, lightweight representation of a person for use in search results
 * @typedef Person
 * @type {object}
 * @property {string}     id             - a unique identifier for the person
 * @property {EntryMeta}  meta           - metadata about the record and its history in the system
 * @property {PersonName} name           - structured name representation
 * @property {string}     label          - formatted name to display
 * @property {string}     summaryExcerpt - a leading excerpt from the full summary
 */

/**
 * Metadata about a TRC entry
 * @typedef EntryMeta
 * @type {object}
 * @property {string}                      slug         - a short, unique identifier intended to be more human-readable than the id
 * @property {integer}                     version      - a monotonically increasing version number incremented on each change
 * @property {object.<string, SimpleLink>} links        - links to related resources; keyed by link role
 * @property {string}                      lastModified - ISO 8601 timestamp of the last modification date
 */

/**
 * Container for linking to related entities
 * @typedef SimpleLink
 * @type {object}
 * @property {string} uri  - a URI representing the linked item
 * @property {string} type - MIME type of the related linked item
 */

/**
 * Structured form of a person's name
 * @typedef PersonName
 * @type {object}
 * @property {string} role        - clarifies the use of this name
 * @property {string} label       - a simple representation of this name for display purposes
 * @property {?string} title      - title of address
 * @property {?string} givenName  - given or first name
 * @property {?string} middleName - middle name
 * @property {?string} familyName - family or last name
 * @property {?string} suffix     - a suffix (e.g. 'Jr' or 'III')
 */

/**
 * Event with a date/time, location, and descriptive information
 * @typedef HistoricalEvent
 * @type {object}
 * @property {string}          id          - a unique identifier for this event
 * @property {string}          title       - a brief title of the event for display purposes
 * @property {string}          description - a detailed summary of the event
 * @property {string}          location    - the location in which the event took place
 * @property {DateDescription} date        - the date the event took place
 */

/**
 * Container for encapsulating human-readable and machine-readable forms of a date.
 * @typedef DateDescription
 * @type {object}
 * @property {string} calendar    - machine-readable ISO 8601 timestamp
 * @property {string} description - human-readable description of this date
 */

/**
 * Container for search results
 * @typedef PersonSearchResultSet
 * @type {object}
 * @property {SimplePerson[]} items  - search results
 * @property {string}         qs     - query string that gives the current set of search results
 * @property {?string}        qsNext - query string that gives next set of search results or null if this is the last set
 * @property {?string}        qsPrev - query string that gives preceding set of search results or null if this is the first set
 */

(function () {
  'use strict';

  angular
    .module('trcBio')
    .provider('peopleRepo', peopleRepoProvider);


  function peopleRepoProvider() {
    var provider = {};
    provider.url = '/api/people';
    provider.$get = peopleRepoFactory;
    return provider;

    /** @ngInject */
    function peopleRepoFactory($q, $resource) {
      var restResource = $resource(provider.url + '/:id', null, {
        'update': { method: 'PUT' }
      });

      var repo = {};
      repo.create = createPerson;
      repo.createName = createName;
      repo.createEvent = createEvent;
      repo.get = findById;
      repo.search = search;
      repo.save = savePerson;
      repo.delete = deletePerson;
      return repo;

      /**
       * Creates a new empty person instance.
       *
       * @return {Person}
       */
      function createPerson() {
        var person = new restResource();
        return adaptPerson(person);
      }

      /**
       * Creates a new empty entry metadata instance
       *
       * @return {EntryMeta}
       */
      function createMeta() {
        var meta = {};
        return adaptMeta(meta);
      }

      /**
       * Creates a new empty name instance
       *
       * @return {PersonName}
       */
      function createName() {
        var name = {};
        return adaptName(name);
      }

      /**
       * Creates a new historical event instance
       *
       * @return {HistoricalEvent}
       */
      function createEvent() {
        var evt = {};
        return adaptEvent(evt);
      }

      /**
       * Creates a date/description instance
       *
       * @return {DateDescription}
       */
      function createDateDescription() {
        var dateDescription = {};
        return dateDescription;
      }

      /**
       * Retrieves a person object by identifier.
       *
       * A proxy object will be returned immediately, and its properties will be populated after the
       * request succeeds. If fine-grained asynchronous handling, etc. is required, the request's
       * promise is available on Person.$promise.
       *
       * @param  {string} id
       * @return {Person}
       */
      function findById(id) {
        return restResource.get({ id: id });
      }

      /**
       * Retrieves a serch result set of people who match the given query.
       *
       * @param {string} query
       * @return {PersonSearchResultSet}
       */
      function search(query) {
        return restResource.get({ q: query });
      }

      /**
       * Saves a person model to the REST API.
       *
       * If no ID is provided on the person model, the API will create save the model as a new
       * person. Otherwise, the API will attempt to update the existing person model with that ID.
       *
       * @param {Person} person
       * @return {Promise.<Person>} - resolves to the saved person on success
       */
      function savePerson(person) {
        var response;
        if (person.id) {
          response = restResource.update({ id: person.id }, person);
        } else {
          response = restResource.save(null, person);
        }

        return response.$promise.then(function (personId) {
          person.id = personId.id;
          return person;
        });
      }

      /**
       * Deletes a person on the server via the REST API.
       *
       * @param {Person|string} idOrPerson
       * @return {Promise} - resolves on success
       */
      function deletePerson(id) {
        var response = restResource.delete({ id: id });
        return response.$promise;
      }

      /**
       * Populates a person with aggregate fields
       *
       * @param  {Person} person
       * @return {Person}
       */
      function adaptPerson(person) {
        if (!person.meta) {
          person.meta = createMeta();
        } else {
          adaptMeta(person.meta);
        }

        if (!person.name) {
          person.name = createName();
        } else {
          adaptName(person.name);
        }

        if (!person.altNames) {
          person.altNames = [];
        } else {
          person.altNames.forEach(adaptName);
        }

        if (!person.birth) {
          person.birth = createEvent();
        } else {
          adaptEvent(person.birth);
        }

        if (!person.death) {
          person.death = createEvent();
        } else {
          adaptEvent(person.death);
        }

        return person
      }

      /**
       * Ensure that entry metadata aggregate fields exist and are formatted correctly.
       *
       * @param  {EntryMeta} meta
       * @return {EntryMeta}
       */
      function adaptMeta(meta) {
        if (!meta.links) {
          meta.links = {};
        }

        return meta;
      }

      /**
       * Ensure that name aggregate fields exist and are formatted correctly
       * @param  {PersonName} name
       * @return {PersonName}
       */
      function adaptName(name) {
        // no aggregate fields yet...
        return name;
      }

      /**
       * Ensure that event aggregate fields exist and are formatted correctly.
       *
       * @param  {HistoricalEvent} evt
       * @return {HistoricalEvent}
       */
      function adaptEvent(evt) {
        if (!evt.date) {
          evt.date = createDateDescription();
        }

        return evt;
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('trcBio')
    .directive('trcPersonEditor', trcPersonEditorDirective);

  /** @ngInject */
  function trcPersonEditorDirective() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/trc-bio/person-editor.html',
      scope: {
        person: '=ngModel'
      },
      controller: PersonEditorController,
      controllerAs: 'personEditor'
    };

    return directive;

    /** @ngInject */
    function PersonEditorController($scope, peopleRepo) {
      var vm = this;

      vm.addName = addName;
      vm.removeName = removeName;
      vm.addEvent = addEvent;
      vm.removeEvent = removeEvent;

      /**
       * Adds a name to the current person model.
       */
      function addName() {
        var newName = peopleRepo.createName();
        $scope.person.altNames.push(newName);
      }

      /**
       * Removes the name at the specified index from the current person model.
       *
       * @param {integer} ix
       */
      function removeName(ix) {
        $scope.person.altNames.splice(ix, 1);
      }

      /**
       * Adds an event to the current person model.
       *
       * @param {string} key - 'birth' or 'death'
       */
      function addEvent(key) {
        var newEvent = peopleRepo.createEvent();
        $scope.person[key] = newEvent;
      }

      /**
       * Removes the event from the current person model.
       *
       * @param {string} key - 'birth' or 'death'
       */
      function removeEvent(key) {
        $scope.person[key] = null;
      }
    }
  }

})();

